import { Component, OnInit } from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IAddSoilTypes, IFarmedType} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddEditIrrigationWaysComponent} from '../../farms/irrigation-ways/add-edit-irrigation-ways/add-edit-irrigation-ways.component';
import {DeleteIrrigationWaysComponent} from '../../farms/irrigation-ways/delete-irrigation-ways/delete-irrigation-ways.component';

@Component({
  selector: 'app-farmed-type-generall-info',
  templateUrl: './farmed-type-generall-info.component.html',
  styleUrls: ['./farmed-type-generall-info.component.css']
})
export class FarmedTypeGenerallInfoComponent implements OnInit {

  cols: any;
  products: any;
  roles: IAppAdmin[];
  farms: IAddSoilTypes[];
  profileFarm: IFarmedType;
  rolesPermations: any[];
  display: boolean = false;
  data: any;
  // toastOption: ToastOptions = {
  //     title: 'Events',
  //     msg: 'This Event Has Been Deleted successfully!',
  //     showClose: true,
  //     timeout: 3000,
  //     theme: 'material'
  // };
  errorMessage: string;
  profileFarmData: IAddFarmedType;

  constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
  }

  //
  ngOnInit(): void {
    this.cols = [
      // {field: 'id', header: 'ID'},
      {field: 'assets', header: 'Image'},

      {field: 'farmed_type', header: 'farmed_type'},
      {field: 'farmed_type_stage', header: 'farmed_type_stage'},
      {field: 'title_ar_localized', header: 'title_ar_localized'},
      {field: 'content_ar_localized', header: 'content_ar_localized'},


    ];
    this.getFarmsApp();
  }

  // showDialogGetFarmedTypeById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarm = res.data as IFarmedType;
  //         console.log(this.profileFarm);
  //     }, error => {
  //
  //     }, () => {
  //         this.display = true;
  //
  //     });
  // }

  getFarmsApp() {
    this._FarmsService.getFarmed_type_info().subscribe((res: { data }) => {
      this.farms = res.data.all as IAddSoilTypes[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }


  openAddFarmedTypeDialog() {

    const dialogRef = this.dialog.open(AddEditIrrigationWaysComponent, {
          data: {
            FarmedData: this._FarmsService.AddIrrigationWays, error: this.errorMessage,
          }
        })
    ;
    dialogRef.afterClosed().subscribe(result => {
      this.errorMessage = null;

      if (result) {
        this._FarmsService.AddIrrigationWays = result.FarmedData;
        this.onAddFarmedType();

      } else {
        this._FarmsService.AddIrrigationWays = {} as IAddSoilTypes;

      }
    });
  }

  onAddFarmedType() {
    this._FarmsService.AddFarmed_type_info().subscribe(res => {
    }, error => {

      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddIrrigationWays = {} as IAddSoilTypes;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  // getDataById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarmData = res.data as IAddFarmedType;
  //         console.log(this.profileFarmData);
  //     }, error => {
  //
  //     }, () => {
  //         // this.display = true;
  //     });
  //
  //
  // }

  openEditFarmedTypeDialog(classData: IAddSoilTypes) {


    const dialogRef = this.dialog.open(AddEditIrrigationWaysComponent, {
      data: {FarmedData: classData}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this._FarmsService.AddIrrigationWays.name_ar_localized = result.FarmedData.name_ar_localized;
        this._FarmsService.AddIrrigationWays.name_en_localized = result.FarmedData.name_en_localized;

        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onEditFarmedType(result.FarmedData.id);

      } else {
        this._FarmsService.AddIrrigationWays = {} as IAddSoilTypes;

      }
    });
  }

  onEditFarmedType(roleId) {
    this._FarmsService.editFarmed_type_info(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddIrrigationWays = {} as IAddSoilTypes;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Updated successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  openFarmedTypeDeleteDialog(data) {
    const dialogRef = this.dialog.open(DeleteIrrigationWaysComponent, {
      data: {farmData: data}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onDeleteFarmedType(result);

      }
    });
  }

  onDeleteFarmedType(roleId) {
    this._FarmsService.deleteFarmed_type_info(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }


}
